import { jsPDF } from 'jspdf';
import 'jspdf-autotable';

const MAROON = [114, 47, 55];
const GRAY = [100, 100, 100];

export async function generateInvoicePDF(invoice) {
  const doc = new jsPDF({ unit: 'mm', format: 'a4' });
  const pageWidth = doc.internal.pageSize.getWidth();
  let y = 20;

  // Company logo (try to load from public)
  try {
    const logoUrl = `${window.location.origin}/logo.png`;
    const imgData = await loadImageAsBase64(logoUrl);
    if (imgData) {
      doc.addImage(imgData, 'PNG', 15, 12, 35, 18);
    }
  } catch (e) {
    // Fallback: company name text
    doc.setFontSize(18);
    doc.setTextColor(...MAROON);
    doc.setFont(undefined, 'bold');
    doc.text('Kamaxi Wood Industries', 15, 22);
    doc.setFont(undefined, 'normal');
    doc.setFontSize(9);
    doc.setTextColor(...GRAY);
    doc.text('Custom Wooden Furniture', 15, 28);
  }

  // Company details (right side)
  doc.setFontSize(10);
  doc.setTextColor(...GRAY);
  doc.text('Hubli Road, Mundgod - 581349', pageWidth - 15, 18, { align: 'right' });
  doc.text('Karnataka, India', pageWidth - 15, 24, { align: 'right' });
  doc.text('GSTIN: 29XXXXXXXXXXX', pageWidth - 15, 30, { align: 'right' });

  y = 38;

  // Invoice info block
  doc.setDrawColor(...MAROON);
  doc.setLineWidth(0.5);
  doc.rect(15, y, pageWidth - 30, 32);
  doc.setFontSize(11);
  doc.setTextColor(...MAROON);
  doc.setFont(undefined, 'bold');
  doc.text('INVOICE', 15, y + 8);

  doc.setFont(undefined, 'normal');
  doc.setFontSize(9);
  doc.setTextColor(0, 0, 0);
  doc.text(`Invoice No: ${invoice.invoiceNumber || 'N/A'}`, 15, y + 16);
  doc.text(`Order ID: ${invoice.order?.orderNumber || 'N/A'}`, 15, y + 22);
  doc.text(`Date: ${new Date(invoice.createdAt).toLocaleDateString('en-IN')}`, 15, y + 28);

  doc.text(`Customer: ${invoice.customer?.name || 'N/A'}`, pageWidth - 15, y + 16, { align: 'right' });
  doc.text(`Phone: ${invoice.customer?.phone || 'N/A'}`, pageWidth - 15, y + 22, { align: 'right' });
  const addr = invoice.order?.deliveryAddress
    ? [invoice.order.deliveryAddress.street, invoice.order.deliveryAddress.city, invoice.order.deliveryAddress.state, invoice.order.deliveryAddress.zipCode].filter(Boolean).join(', ')
    : [invoice.customer?.address?.street, invoice.customer?.address?.city, invoice.customer?.address?.state].filter(Boolean).join(', ');
  doc.text(addr || 'N/A', pageWidth - 15, y + 28, { align: 'right' });

  y += 40;

  // Items table
  const tableData = (invoice.items || []).map((item) => [
    item.productName || 'Furniture Item',
    item.description || 'Custom size / material',
    String(item.quantity || 1),
    `${Number(item.price || 0).toLocaleString()}`,
    `${Number(item.subtotal || item.price * (item.quantity || 1)).toLocaleString()}`
  ]);

  doc.autoTable({
    startY: y,
    head: [['Furniture Item', 'Description', 'Qty', 'Rate ()', 'Amount ()']],
    body: tableData,
    theme: 'grid',
    headStyles: { fillColor: MAROON, textColor: [255, 255, 255] },
    margin: { left: 15, right: 15 }
  });

  y = doc.lastAutoTable.finalY + 15;

  // GST section - use invoice values; if tax exists split as CGST/SGST
  const subtotal = Number(invoice.subtotal || 0);
  const tax = Number(invoice.tax || 0);
  const cgst = tax > 0 ? tax / 2 : subtotal * 0.09;
  const sgst = tax > 0 ? tax / 2 : subtotal * 0.09;
  const discount = Number(invoice.discount || 0);
  const total = Number(invoice.total || subtotal + tax - discount);

  const rightCol = pageWidth - 65;
  doc.setFontSize(10);
  doc.text('Subtotal:', rightCol, y);
  doc.text(`${subtotal.toLocaleString('en-IN', { minimumFractionDigits: 2 })}`, pageWidth - 15, y, { align: 'right' });
  y += 7;
  doc.text('CGST (9%):', rightCol, y);
  doc.text(`${cgst.toLocaleString('en-IN', { minimumFractionDigits: 2 })}`, pageWidth - 15, y, { align: 'right' });
  y += 7;
  doc.text('SGST (9%):', rightCol, y);
  doc.text(`${sgst.toLocaleString('en-IN', { minimumFractionDigits: 2 })}`, pageWidth - 15, y, { align: 'right' });
  y += 7;
  if (discount > 0) {
    doc.text('Discount:', rightCol, y);
    doc.text(`- ${discount.toLocaleString('en-IN', { minimumFractionDigits: 2 })}`, pageWidth - 15, y, { align: 'right' });
    y += 7;
  }
  doc.setFont(undefined, 'bold');
  doc.setFontSize(12);
  doc.setTextColor(...MAROON);
  doc.text('Grand Total:', rightCol, y + 2);
  doc.text(`${total.toLocaleString('en-IN', { minimumFractionDigits: 2 })}`, pageWidth - 15, y + 2, { align: 'right' });
  doc.setTextColor(0, 0, 0);
  doc.setFont(undefined, 'normal');

  y += 25;

  // Footer
  doc.setFontSize(9);
  doc.setTextColor(...GRAY);
  doc.text(`Payment Status: ${invoice.status || 'Pending'}`, 15, y);
  doc.text('Thank you for your order!', pageWidth / 2, y, { align: 'center' });
  doc.text('Authorized Signature', pageWidth - 15, y, { align: 'right' });

  doc.save(`Invoice-${invoice.invoiceNumber || 'Kamaxi'}.pdf`);
}

function loadImageAsBase64(url) {
  return new Promise((resolve) => {
    const img = new Image();
    img.crossOrigin = 'Anonymous';
    img.onload = () => {
      try {
        const canvas = document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0);
        resolve(canvas.toDataURL('image/png'));
      } catch {
        resolve(null);
      }
    };
    img.onerror = () => resolve(null);
    img.src = url;
  });
}
